.movie {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 0px 5px;
  margin: 0;
}
.movie__poster {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #cacaca;
}

.movie__title {
  position: absolute;

  top: 20px;
  font-weight: 400;
  font-size: 45px;
  margin-bottom: 20px;
  text-align: center;
}
.dark {
  opacity: 0.2;
}
.movie__rating {
  position: absolute;
}
.hidden {
  display: none;
}
.movie__goToDetail {
  position: absolute;
  bottom: 20px;
}
.link {
  text-decoration: none;
  color: #cacaca;
  font-size: 20px;
}
.detail {
  color: #cacaca;
  display: flex;
}
.detail__img__wrapper {
  display: flex;
  height: 100vh;
  align-items: center;
  justify-content: center;
  box-sizing: border-box;
}
.detail__img__wrapper img {
  height: 80vh;
}
.detail__wrapper {
  margin-left: 20px;
  margin-top: 20px;
  flex-wrap: wrap;
}
.detail__title {
  text-align: center;
  font-size: 80px;
  margin-bottom: 30px;
}
.detail__rating {
  margin-bottom: 15px;
}
.detail__genres {
  display: flex;
  margin-bottom: 15px;
}
.detail__genres ul {
  flex-wrap: wrap;
  display: flex;
}
.detail__genres li {
  display: inline;
  margin-left: 15px;
}
