.loading {
  color: #ffe4e1;
  font-size: 100px;
}
.title {
  color: #ffe4e1;
  margin-top: 30px;
  text-align: center;
  font-weight: 600;
  font-size: 80px;
  margin-bottom: 50px;
}
.movieList {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(auto-fil, 1fr);
  list-style: none;
}
